html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

body {
  font-family: Arial, Tahoma, sans-serif;
  font-size: 15px;
  line-height: 15px;
  color: cyan;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding-top: 50px;
  max-width: 700px;
}

.slider-wrapper {
  max-width: 700px;
  width: 700px;
}

.slider {
  display: none;
  margin-bottom: 50px;
  overflow: hidden;
  height: 100%;
  position: relative;
}

.slider.show {
  display: block;
}

.slick-slide {
  display: inline-block;
}

.tabs {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

.tab {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid cyan;
  border-radius: 5px;
  margin-right: 20px;
}

.tab:last-child {
  margin: 0;
}

.tab:hover {
  background: cyan;
}

.tab:hover a {
  color: white;
}

a {
  font-family: Arial, Tahoma, sans-serif;
  font-size: 15px;
  line-height: 15px;
  color: cyan;
  text-decoration: none;
  padding: 10px;
}

h2 {
  color: #000000;
  padding-left: 10px;
  font-size: 18px;
}

.slide {
  border: 1px solid cyan;
  border-radius: 5px;
  display: flex !important;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  position: relative;
  background-color: #ffffff;
}

.slide img {
  width: 100%;
  margin-bottom: 10px;
}

.slide__modal {
  display: none;
}

.description {
  padding-left: 10px;
  color: #000000;
  font-size: 12px;
  margin-bottom: 10px;
  max-width: 300px;
}

.date {
  padding-left: 10px;
  color: cadetblue;
  font-size: 15px;
  margin-bottom: 10px;
}

.btn {
  width: 100%;
  height: 30px;
  background: cyan;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}

.slick-arrow {
  display: block;
  width: 20px;
  height: 20px;
  font-size: 0;
  content: '->';
  position: absolute;
  top: calc(50% - 10px);
  left: 2px;
  background-color: red;
  border-radius: 50%;
  z-index: 10;
}

.slick-next {
  left: calc(100% - 22px);
}

.active {
  background: cyan;
}

.active a {
  color: white;
}

.modal {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  display: none;
  text-align: center;
  z-index: 1000;
}

.modal img {
  max-width: 400px;
  margin: 0;
}

.modal__bg {
  background: rgba(0, 0, 0, 0.8);
  position: absolute;
  z-index: 1;
  height: 100%;
  width: 100%;
}

.modal .slide {
  flex-direction: row;
  justify-content: space-between;
}

.close {
  position: absolute;
  display: block;
  content: '';
  top: 10px;
  right: 10px;
  z-index: 20;
}

.close__item {
  display: block;
  width: 30px;
  height: 5px;
  background-color: red;
  border-radius: 5px;
}

.close__item:first-child {
  transform: rotate(-45deg) translate(5px, 8px);
}

.close__item:last-child {
  transform: rotate(45deg) translate(5px, -8px);
}

.modalSlide-wrapper {
  position: relative;
  z-index: 2;
  width: 700px;
  margin: 0 auto;
  margin-top: 50px;
  overflow: hidden;
}

.modalSlide__img {
  width: 300px;
}

.modalSlide__content {
  padding: 10px;
}

.modalSlide__title {
  margin-bottom: 20px;
  font-size: 25px;
  line-height: 30px;
}

.modalSlide__description {
  width: 100%;
  color: #000;
  font-size: 15px;
  line-height: 20px;
  margin-bottom: 20px;
}

.counter {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #000000;
  position: absolute;
  left: calc(100% - 50px);
  top: calc(100% - 50px);
  background: cyan;
  z-index: 100;
  font-size: 15px;
  padding: 10px;
}
