body {
  font-family: Arial, Tahoma, sans-serif;
  font-size: 15px;
  line-height: 15px;
  color: cyan; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding-top: 50px;
  max-width: 700px; }
.slider-wrapper {
  max-width: 700px;
  width: 700px; }
.slider {
  display: none;
  margin-bottom: 50px;
  overflow: hidden;
  height: 100%;
  position: relative; }
.slider.show {
    display: block; }
.slick-slide {
  display: inline-block; }
.tabs {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px; }
.tab {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid cyan;
  border-radius: 5px;

  margin-right: 20px;
  &:last-child {
    margin: 0; }
  &:hover {
    background: cyan; }
  &:hover a {
    color: white; } }

a {
  font-family: Arial, Tahoma, sans-serif;
  font-size: 15px;
  line-height: 15px;
  color: cyan;
  text-decoration: none;
  padding: 10px; }

h2 {
  color: #000000;
  padding-left: 10px;
  font-size: 18px; }

.slide {
  border: 1px solid cyan;
  border-radius: 5px;
  display: flex!important;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  position: relative;
  background-color: #ffffff;
  img {
    width: 100%;
    margin-bottom: 10px; }
  &__modal {
    display: none; } }



.description {
  padding-left: 10px;
  color: #000000;
  font-size: 12px;
  margin-bottom: 10px;
  max-width: 300px; }

.date {
  padding-left: 10px;
  color: cadetblue;
  font-size: 15px;
  margin-bottom: 10px; }

.btn {
  width: 100%;
  height: 30px;
  background: cyan;
  border-radius: 5px;
  border: none;
  cursor: pointer; }

.slick-arrow {
  display: block;
  width: 20px;
  height: 20px;
  font-size: 0;
  content: '->';
  position: absolute;
  top: calc(50% - 10px);
  left: 2px;
  background-color: red;
  border-radius: 50%;
  z-index: 10; }

.slick-next {
  left: calc(100% - 22px); }


.active {
  background: cyan;
  a {
    color: white; } }

.modal {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  display: none;
  text-align: center;
  z-index: 1000;
  img {
    max-width: 400px;
    margin: 0; }
  &__bg {
    background: rgba(0,0,0,0.8);
    position: absolute;
    z-index: 1;
    height: 100%;
    width: 100%; }
  .slide {
    flex-direction: row;
    justify-content: space-between; } }
.close {
  position: absolute;
  display: block;
  content: '';
  top: 10px;
  right: 10px;
  z-index: 20;
  &__item {
    display: block;
    width: 30px;
    height: 5px;
    background-color: red;
    border-radius: 5px;
    &:first-child {
      transform:  rotate(-45deg) translate(5px,8px); }
    &:last-child {
      transform:  rotate(45deg) translate(5px,-8px); } } }


.modalSlide-wrapper {
  position: relative;
  z-index: 2;
  width: 700px;
  margin: 0 auto;
  margin-top: 50px;
  overflow: hidden; }
.modalSlide {
  &__img {
    width: 300px; }
  &__content {
    padding: 10px; }
  &__title {
    margin-bottom: 20px;
    font-size: 25px;
    line-height: 30px; }
  &__description {
    width: 100%;
    color: #000;
    font-size: 15px;
    line-height: 20px;
    margin-bottom: 20px; } }

.counter {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #000000;
  position: absolute;
  left: calc(100% - 50px);
  top: calc(100% - 50px);
  background: cyan;
  z-index: 100;
  font-size: 15px;
  padding: 10px; }
